import React, { useState, useEffect } from 'react';
import axios from 'axios';
import * as emailjs from 'emailjs-com'
import 'antd/dist/antd.css';
import { message as alert } from 'antd';
import Zoom from 'react-reveal/Zoom';
import Fade from 'react-reveal/Fade';
import Slide from 'react-reveal/Slide'
import getDate from './custom/GetDate'

import { LoadingOutlined } from '@ant-design/icons';

const BlogDetail = (props) => {
    const [blog, setBlog] = useState({});
    const [name, setName] = useState("");
    const [email, setEmail] = useState("");
    const [message, setMessage] = useState("");
    const [isLoading, setLoading] = useState(false)


    const resetForm = () => {
        setName("")
        setEmail("")
        setMessage("")
    }
    const capitalizeFirstLetter = (word) => {
        if (word)
            return word.charAt(0).toUpperCase() + word.slice(1);
        else
            return '';
    };
    const handleSubmit = (e) => {
        e.preventDefault();

        const templateParams = {
            name: capitalizeFirstLetter(name),
            email: email,
            message: message,
            blog_title: blog.title,
            blog_slug: blog.slug
        }
        emailjs.send(
            'gmail',
            'blog_comments',
            templateParams,
            'user_dXe2JFhmZcSr93S8lDBkp'
        )
        resetForm();

        console.log("hello")
        console.log(templateParams)

        alert.success("Successfully Submitted", [3])
    };

    useEffect(() => {
        const slug = props.match.params.id;

        const fetchData = async () => {
            setLoading(true)
            try {
                const res = await axios.get(`${process.env.REACT_APP_API_URL}/../../api/blog/${slug}`);
                setBlog(res.data);
            }
            catch (err) {

            }
            setLoading(false)
        }

        fetchData();
    }, [props.match.params.id]);

    const capitalize = (word) => {
        if (word)
            return word.toUpperCase()
        else
            return '';
    };

    const createBlog = () => {
        return (
            { __html: blog.content }

        );
    };

    return (
        <div className="s-content content">

            {isLoading ? <div className="loading"><LoadingOutlined /></div> :

                <main className="row content__page" style={{ marginBottom: blog.length > 0 ? '0' : '20vh' }}>

                    <article className="column large-full entry format-standard">
                        <div className="media-wrap entry__media">
                            <div className="entry__post-thumb">

                                <Zoom top cascade  ><img
                                    src={blog.thumbnail}
                                    alt=""
                                />
                                </Zoom>
                            </div>
                        </div>
                        <div className="content__page-header entry__header">
                            <h1 className="display-1 entry__title">
                                <Fade top delay={500}>
                                    {blog.title}
                                </Fade>
                            </h1>
                            <ul className="entry__header-meta">
                                <Fade bottom delay={800}>
                                    <li className="entry__meta-cat">

                                        {capitalize(blog.category)}

                                    </li>

                                    <li className="date">
                                        {blog.id > 0 ? getDate(blog.date_created) : null}


                                    </li>
                                </Fade>

                            </ul>
                        </div>{" "}
                        {/* end entry__header */}
                        <Fade left delay={1000}>
                            <div className="entry__content" dangerouslySetInnerHTML={createBlog()} />
                        </Fade>



                        {/* end entry content */}

                    </article>
                    <Slide bottom cascade key={blog.title} >
                        <section className="column large-full entry format-standard" style={{ visibility: blog.length > 0 ? 'visible' : 'hidden' }}>
                            <h3 className="h2">Got to say something about this blog?</h3>

                            <form name="contactForm" id="contactForm" onSubmit={(e) => handleSubmit(e)}>
                                <fieldset>
                                    <div className="form-field">
                                        <input

                                            className="full-width"
                                            placeholder="Your Name"
                                            type="text"
                                            onChange={e => setName(e.target.value)}
                                            value={name} />
                                    </div>
                                    <div className="form-field">
                                        <input

                                            className="full-width"
                                            placeholder="Your Email"
                                            type="text"
                                            onChange={e => setEmail(e.target.value)}
                                            value={email} />
                                    </div>
                                    <div className="message form-field">
                                        <textarea

                                            className="full-width"
                                            placeholder="Your Message"

                                            onChange={e => setMessage(e.target.value)}
                                            value={message} />
                                    </div>
                                    <input className="btn btn--primary btn-wide btn--large full-width" defaultValue="Send Message" type="submit" />

                                </fieldset>
                            </form>

                        </section>
                    </Slide>
                </main>}
        </div>


    );
}

export default BlogDetail;