import React, { useState } from 'react';
import 'react-slidedown/lib/slidedown.css'
import { Link, NavLink } from 'react-router-dom';
import Fade from 'react-reveal/Fade';



function Header() {

    const [searchOpen, setSearchOpen] = useState(false);
    const [toggleMenu, setToggleMenu] = useState(false);







    const HeaderContent = () => {
        return (

            <nav className="header__nav-wrap" >

                <ul className="header__nav" >
                    <li ><NavLink exact to="/" activeClassName="current" onClick={() => setToggleMenu(false)}>Home</NavLink></li>
                    <li>
                        <Link to="/">Blogs</Link>
                        <ul className="sub-menu">
                            <li><NavLink activeClassName="current" exact to='/categories/cricket' onClick={() => setToggleMenu(false)}>Cricket</NavLink></li>
                            <li><NavLink activeClassName="current" exact to='/categories/trending' onClick={() => setToggleMenu(false)}>What's Trending?</NavLink></li>
                            <li><NavLink activeClassName="current" exact to='/categories/literary' onClick={() => setToggleMenu(false)}>Literary work</NavLink></li>

                        </ul>
                    </li>




                    <li ><NavLink activeClassName="current" to="/about" onClick={() => setToggleMenu(false)}>About</NavLink></li>
                    <li ><NavLink activeClassName="current" to="/contact" onClick={() => setToggleMenu(false)}>Contact</NavLink></li>
                </ul>

                {/* end header__nav */}

                <ul className="header__social">
                    <li className="ss-facebook">
                        <a href="https://www.facebook.com/rishabh.beniwal">
                            <span className="screen-reader-text">Facebook</span>
                        </a>
                    </li>
                    <li className="ss-instagram">
                        <a href="https://www.instagram.com/rishabhbeniwal27/">
                            <span className="screen-reader-text">Instagram</span>
                        </a>
                    </li>
                    <li className="ss-youtube">
                        <a href="#0">
                            <span className="screen-reader-text">Youtube</span>
                        </a>
                    </li>
                    <li className="ss-linkedin">
                        <a href="https://www.linkedin.com/in/rishabh-beniwal-0b714a197/">
                            <span className="screen-reader-text">Linkedin</span>
                        </a>
                    </li>
                    <li className="ss-email">
                        <a href="mailto:rishabhbeniwal27@gmail.com">
                            <span className="screen-reader-text">Email</span>
                        </a>
                    </li>

                </ul>

            </nav>
        )
    };

    const Search = () => {


        const [searchTerm, setSearchTerm] = useState("");

        const handleChange = event => {
            setSearchTerm(event.target.value);

        };

        const searchClose = () => {
            setSearchOpen(!searchOpen)
            setToggleMenu(false)

        }

        const handleKeyPress = (e) => {

            if (e.charCode === 13) {
                e.preventDefault();


            }
        }

        return (

            <div className="search-is-visible" >

                <div className="header__search" >
                    <Fade>
                        <form role="search" className="header__search-form">
                            <label>
                                <span className="hide-content">Search for:</span>
                                <input type="search" className="header__search-field" placeholder="Type Keywords" title="Search for:" onChange={handleChange} value={searchTerm} onKeyPress={(e) => handleKeyPress(e)} />
                            </label>

                            <Link exact to={`/search/${searchTerm}`} onClick={() => searchClose()} className="btn" >Search</Link>


                        </form>

                        <a href="#" title="Close Search" className="header__search-close" onClick={() => searchClose()}>Close</a>
                    </Fade>
                </div>


            </div>


        )

    }


    if (searchOpen) {
        return (<Search />);
    }
    else {
        return (
            <div>

                <header className="s-header">


                    <div className="header__top">

                        <div className="header__logo">
                            <Link className="site-logo" to="/">
                                <img src={require("../images/logo.jpg")} alt="" />
                            </Link>
                        </div>





                        {/* end header__search */}
                        {/* toggles */}
                        <Link to="#" className="header__search-trigger" onClick={() => setSearchOpen(!searchOpen)} />
                        <Link to="#" className={toggleMenu ? "header__menu-toggle is-clicked" : "header__menu-toggle"} onClick={() => setToggleMenu(!toggleMenu)} ><span>Menu</span></Link>
                    </div> {/* end header__top */}
                    {toggleMenu ?

                        <div className="nav-wrap-is-visible">
                            <HeaderContent />

                        </div>
                        :

                        <HeaderContent />

                    }

                </header>

            </div>
        );

    }
}

export default (Header)