import React, {
    useState, useEffect
} from 'react';
import axios from 'axios';
import Masonry from 'react-masonry-css'
import 'antd/dist/antd.css';
import { Card, message as alert } from 'antd';
import { Link } from 'react-router-dom'
import Slide from 'react-reveal/Slide'
import useViewport from './custom/UseViewport'
import Paginator from 'react-hooks-paginator';
import getDate from './custom/GetDate';
import { LoadingOutlined } from '@ant-design/icons';


const RetrieveSearch = (props) => {
    var pageLimit = 15;
    const searchTerm = props.match.params.id
    const [blogs, setBlogs] = useState([]);
    const [currentData, setCurrentData] = useState([]);
    const [offset, setOffset] = useState(0);
    const [currentPage, setCurrentPage] = useState(1);
    const { width } = useViewport();
    const [isLoading, setLoading] = useState(false)

    if (width < 500) {
        pageLimit = 10
    }
    else if (width < 800) {
        pageLimit = 15

    }

    else {
        pageLimit = 20
    }

    const getBreakPoint = () => {
        if (width < 700) {
            return 1
        }
        else if (blogs.length < 10 || width < 1200) {
            return 2
        }
        else if (width < 1600) {
            return 3
        }
        else {
            return 4
        }
    }



    useEffect(() => {
        const fetchBlogs = async () => {
            setLoading(true)
            try {

                const res = await axios.get(`${process.env.REACT_APP_API_URL}/../../api/blog/`);
                var results = res.data.filter((blog) => {
                    if ((blog.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
                        blog.summary.toLowerCase().includes(searchTerm.toLowerCase()) ||
                        blog.content.toLowerCase().includes(searchTerm.toLowerCase())))
                        return blog

                })

                setBlogs(results);

            }
            catch (err) {
                console.log(err)


            }
            setLoading(false)
            if (results.length === 0) {
                alert.error(`Sorry, no blog found which matches "${searchTerm}"`, [3])
            }
            else {
                alert.success(`Found ${results.length} ${results.length === 1 ? "result" : "results"} matching with "${searchTerm}"`, [3])
            }
        }
        fetchBlogs();
    }, []);

    useEffect(() => {
        window.scrollTo(0, 0)
    }, [])




    useEffect(() => {
        setCurrentData(blogs.slice(offset, offset + pageLimit));
    }, [offset, blogs, pageLimit]);



    const getSearchData = () => {


        return (
            currentData.map(blogPost => {
                return (

                    <Link to={`/blog/${blogPost.slug}`} className="stretched-link" key={blogPost.title}>
                        <Slide bottom opposite cascade delay={40} key={blogPost.title}>
                            <Card
                                hoverable
                                style={{ width: "100%", marginBottom: "40px" }}
                                cover={<img src={blogPost.thumbnail} alt="" />}
                                bordered={true}
                            >
                                <div className="entry__text">
                                    <div className="entry__header">

                                        <h2 className="entry__title">{blogPost.title}
                                        </h2>
                                        <div className="entry__meta">
                                            <span className="entry__meta-cat">
                                                {blogPost.category} |
                                        </span>
                                            <span className="entry__meta-date">
                                                {getDate(blogPost.date_created)}
                                            </span>
                                        </div>
                                    </div>
                                    <div className="entry__excerpt">
                                        <p>
                                            {blogPost.summary}
                                        </p>
                                    </div>
                                </div>
                            </Card>
                        </Slide>
                    </Link>

                )
            }))


    };





    return (




        <div className="s-content">

            {isLoading ? <div className="loading"><LoadingOutlined /></div> :

                <div>

                    <Masonry
                        breakpointCols={getBreakPoint()}
                        className="my-masonry-grid"
                        columnClassName="my-masonry-grid_column">
                        {getSearchData()}
                    </Masonry>

                    {/* end masonry */}
                    {/* end masonry-wrap */}
                    <div className="row">
                        <div className="column large-full" style={{ marginBottom: currentData.length > 2 ? '0' : '100vh' }}>
                            <nav className="pgn">
                                <Paginator
                                    onClick={window.scrollTo(0, 0)}
                                    totalRecords={blogs.length}
                                    pageLimit={pageLimit}
                                    pageNeighbours={3}
                                    setOffset={setOffset}
                                    currentPage={currentPage}
                                    setCurrentPage={setCurrentPage}
                                    pageLinkClass={"pgn__num"}
                                    pageNextClass={"pgn__next"}
                                    pagePrevClass={"pgn__prev"}

                                />
                            </nav>
                        </div>
                    </div>
                </div>}
        </div>

    )
};

export default RetrieveSearch;