import React, { useState } from 'react'

const ScrollArrow = () => {
    const [showScroll, setShowScroll] = useState(false)


    const checkScrollTop = () => {
        if (!showScroll && window.pageYOffset > 400) {
            setShowScroll(true)
        } else if (showScroll && window.pageYOffset <= 400) {
            setShowScroll(false)
        }
    };


    const scrollTop = () => {
        window.scrollTo({ top: 0, behavior: 'smooth' });
    };

    window.addEventListener('scroll', checkScrollTop)

    return (
        <div className={showScroll ? "go-top link-is-visible" : "go-top"}>
            <button className="smoothscroll" title="Back to Top" onClick={scrollTop} />
        </div>
    )

}


const Footer = () => {



    return (
        <footer className="s-footer">
            <div className="row">
                <div className="column large-full footer__content">
                    <div className="footer__copyright">
                        <span>© Copyright Typerite 2019</span>
                        <span>Design and Template by <a href="https://www.styleshout.com/">StyleShout</a></span>
                    </div>
                </div>
            </div>
            <ScrollArrow />

        </footer>
    )
}

export default Footer