import React, { useState, useEffect } from 'react'
import * as emailjs from 'emailjs-com'
import 'antd/dist/antd.css';
import { message as alert } from 'antd';
import Fade from 'react-reveal/Fade';
import Slide from 'react-reveal/Slide'

const Contact = () => {

    const [name, setName] = useState("");
    const [email, setEmail] = useState("");
    const [message, setMessage] = useState("");

    useEffect(() => {
        window.scrollTo(0, 0)
    }, [])

    const resetForm = () => {
        setName("")
        setEmail("")
        setMessage("")
    }

    const capitalizeFirstLetter = (word) => {
        if (word)
            return word.charAt(0).toUpperCase() + word.slice(1);
        else
            return '';
    };

    const handleSubmit = (e) => {
        e.preventDefault();
        const templateParams = {
            name: capitalizeFirstLetter(name),
            email: email,
            message: message,
        }
        emailjs.send(
            'gmail',
            'contact',
            templateParams,
            'user_dXe2JFhmZcSr93S8lDBkp'
        )
        resetForm();

        console.log("hello")
        console.log(templateParams)
        alert.success("Successfully submitted", [1.5])
    };




    return (



        <div className="s-content content">
            <main className="row content__page">
                <section className="column large-full entry format-standard">
                    <div className="media-wrap">
                        <div>
                            <Fade top duration={700}>
                                <img src={require("../images/thumbs/contact/contact-500.jpg")} alt="" />
                            </Fade>
                        </div>
                    </div>
                    <Fade bottom duration={700} delay={500}>
                        <div className="content__page-header">
                            <h1 className="display-1">
                                Get In Touch.
        </h1>
                        </div>
                    </Fade>
                    <Slide bottom delay={800}> {/* end content__page-header */}
                        <p className="contact-p">
                            For collaboration or work related communication, contact me at <a href="mailto:rishabhbeniwal27@gmail.com">rishabhbeniwal27@gmail.com</a>
                        </p>
                    </Slide>
                    <Slide bottom>
                        <div>
                            <h3 className="h2">Say Hello</h3>

                            <form name="contactForm" id="contactForm" onSubmit={(e) => handleSubmit(e)}>
                                <fieldset>
                                    <div className="form-field">
                                        <input

                                            className="full-width"
                                            placeholder="Your Name"
                                            type="text"
                                            onChange={e => setName(e.target.value)}
                                            value={name} />
                                    </div>
                                    <div className="form-field">
                                        <input

                                            className="full-width"
                                            placeholder="Your Email"
                                            type="text"
                                            onChange={e => setEmail(e.target.value)}
                                            value={email} />
                                    </div>
                                    <div className="message form-field">
                                        <textarea

                                            className="full-width"
                                            placeholder="Your Message"

                                            onChange={e => setMessage(e.target.value)}
                                            value={message} />
                                    </div>
                                    <input className="btn btn--primary btn-wide btn--large full-width" defaultValue="Send Message" type="submit" />

                                </fieldset>
                            </form>
                        </div>
                    </Slide>
                </section>
            </main>
        </div>
    )
}

export default Contact    