import React, { useEffect } from 'react'
import Fade from 'react-reveal/Fade';
import Slide from 'react-reveal/Slide'


const About = () => {

    useEffect(() => {
        window.scrollTo(0, 0)
    }, [])


    return (


        <div className="s-content content">
            <main className="row content__page">
                <section className="column large-full entry format-standard">
                    <div className="media-wrap">
                        <div className="profile-pic">
                            <Fade top duration={700}><img src={require("../images/my_picture.jpg")} alt="" />
                            </Fade>
                        </div>
                    </div>
                    <Fade bottom duration={700} delay={1000}>
                        <div className="content__page-header">

                            <h1 className="display-1 about-h1">

                                Hello, This is Rishabh Beniwal
        </h1>
                        </div>
                    </Fade>
                    <Slide bottom delay={1300}>
                        {/* end content__page-header */}
                        <p className="about-p">Welcome to my blog. This blog will cover everything that is even remotely related to cricket. Best cricket matches, knocks, spells, movies on cricket, books of cricket, cricket games, series, etc you name it, you will find all of it here. It’s a blog like never before, I’m here to serve you, ‘the audience’ of this beloved game. You will find interesting stats, background stories, never-heard-before facts, and much more. You will also find articles on trending topics and some of my literary work. But the focus will forever be cricket since it’s my first love.
      </p></Slide> <Slide bottom>
                        <p className="about-p">
                            I know what it takes to be a cricket fan. People will never understand our sacrifices. The love for the game can wake us up early morning at 5 am or can make us stay up till 3-4 a.m. Missing classes and tuitions to watch the game is what we love to do the most. Whether it’s about gasping as the ball passes the edge of the bat or appealing at the top of our voice for an lbw, we’re pro at it.

      </p>

                        <p className="about-p">We as fans remember everything, whether it’s the number of balls Yuvraj Singh took to hit 50 when he smashed 6 sixes or the commentary of India’s 2011 World Cup win moment. Whether it’s the largest margin of victory for India in overseas tests or the number of sixes Virender Sehwag hit in his test career, we remember it all. The numbers, the commentary, the dates all etched in our memories forever. So I welcome all you fans to my abode of cricket. We’re here to tell people that holding your pee just to not miss a moment of the game is an art that everyone can’t master.
      </p>
                    </Slide><hr />
                </section>
            </main >
        </div >)
}

export default About
